body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
  display: flex;
  flex-direction: row;
  height: 60px;
  width: 100%;
  background-color: #ffc8cd;
}
.nav__img {
  height: 40px;
  margin: 10px 20px;
  border-radius: 50%;
}
.nav__title {
  color: #555555;
  font-size: 20px;
  line-height: 60px;
}
.nav__list {
  flex: 1 1;
  margin-left: 40px;
  display: flex;
  flex-direction: row;
  list-style: none;
  line-height: 60px;
  color: #555555;
}
.nav__list .list__item {
  margin: 0 10px;
}

.about {
  height: 100%;
  background: url(/resume/static/media/bg1.fb41d974.jpeg) center no-repeat;
  background-size: cover;
}
.about__container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.about__block {
  display: flex;
  flex-direction: column;
  height: 400px;
  width: 400px;
  align-items: center;
  border-radius: 10px;
  opacity: 0.85;
  background-color: #eee;
}
.about__block .block__info {
  display: flex;
  flex-direction: column;
  margin-top:20px; 
}
.about__block .block__info .info__avatar {
  position: relative;
  display: flex;
  height: 100px;
  justify-content: center;
}
.about__block .block__info .info__avatar .avatar__line{
  position: absolute;
  display: inline-block;
  top: 50px;
  width: 70px;
  border-top: 1px solid #ccc ;
}
.about__block .block__info .info__avatar .avatar__line--left{
  left: -70px
}
.about__block .block__info .info__avatar .avatar__line--right{
  left: 120px
}
.about__block .block__info .info__avatar img {
  height: 100%;
  border-radius: 50%;
  position: relative;
  vertical-align: middle;
}
.about__block .block__info .info__name {
  padding-top: 20px;
  text-align: center;
  font-size: 26px;
  color: #555;
}
.about__block .block__job {
  width: 200px;
  padding-top: 20px;
  text-align: center;
  font-size: 18px;
  color: #555;
}
.about__block .block__concat {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-top: 20px;
  width: 200px;
}
.about__block .block__concat .concat__list {
  display: flex;
  flex-direction: column;
  list-style: none;
}
.about__block .block__concat .concat__list .list__item {
  display: flex;
  flex-direction: row;
  margin: 5px 0;
  font-size: 0;
}
.about__block .block__concat .concat__list .list__item .item__icon {
  height: 20px;
}
.about__block .block__concat .concat__list .list__item .item__icon img {
  height: 100%;
}
.about__block .block__concat .concat__list .list__item .item__detail {
  padding-left: 10px;
  line-height: 20px;
  font-size: 16px;
  color: #555;
}
.about__block .block__link {
  display: flex;
  flex-direction: row;
  padding-top:20px; 
  list-style: none;
}
.about__block .block__link .link__item {
  height: 20px;
  padding: 0 10px;
}
.about__block .block__link .link__item img{
  height: 100%;
}

html,body{
  height: 100%;
}

#root,.App{height: 100%;}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}
